import React from 'react'
import { AppContent, AppSidebar, AppFooter, AppHeader } from '../components/index'
import Roles from '../helpers/roles'
const DefaultLayout = () => {
  let user = localStorage.getItem('user')
  if(user){
    var userType = JSON.parse(localStorage.getItem('user')).role
  } else {
    var userType = 'default'
  }
  return (
    <div>
      <AppSidebar />
      <div className="wrapper d-flex flex-column min-vh-100 bg-light">
        <AppHeader />
        <div className="body flex-grow-1 px-3">
        {(() => {
          switch (userType) {

            case Roles.EV_SUPER_ADMIN:

                return (

                  <AppContent />

                )
            case Roles.EV_ADMIN:

              return (

                <div>You are a admin</div>

              )
              case Roles.STUDENT:

              return (

                <div>You are a student</div>

              )
              case Roles.TEACHER:

              return (

                <div>You are a TEACHER</div>

              )
              case Roles.SUBJECT_TEACHER:

                return (
  
                  <div>You are a TEACHER</div>
  
                )
            default:

                return (

                  <AppContent />

                )

          }
          })()}
        </div>
        <AppFooter />
      </div>
    </div>
  )
}

export default DefaultLayout
