import React from 'react'
import CIcon from '@coreui/icons-react'
import {
  cilSpeedometer,
} from '@coreui/icons'
import {
  faBook,
  faSchool,
  faTachometerAlt
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { CNavGroup, CNavItem, CNavTitle } from '@coreui/react-pro'

const _evAdmin = [
  
  {
    component: CNavItem,
    name: 'Dashboard',
    to: '/dashboard',
    icon: <FontAwesomeIcon icon={faTachometerAlt} className="nav-icon"/>,
  },
  {
    component: CNavGroup,
    name: 'School Management',
    to: '/school-management',
    icon: <FontAwesomeIcon icon={faSchool} className="nav-icon"/>,
    items: [
      {
        component: CNavItem,
        name: 'Create Normal School',
        to: '/school-management/create-normal-school',
      },
      {
        component: CNavItem,
        name: 'Create Chain School',
        to: '/school-management/create-chain-school',
      },
      {
        component: CNavItem,
        name: 'All Normal Schools',
        to: '/school-management/all-normal-schools',
      },
      {
        component: CNavItem,
        name: 'All Chain Schools',
        to: '/school-management/all-chain-schools',
      },
      {
        component: CNavItem,
        name: 'User Management',
        to: '/school-management/user-management',
      },
    ],
  },  

  {
    component: CNavGroup,
    name: 'Subject Management',
    to: '/subject-management',
    icon: <FontAwesomeIcon icon={faBook} className="nav-icon"/>,
    items: [
      {
        component: CNavItem,
        name: 'All Subjects',
        to: '/subject-management/view-all-subjects',
      },
    ],
  },

]

export default _evAdmin
